import React, { useState } from 'react';
import './App.css';
import { Container, Menu, Icon } from "semantic-ui-react";

import About from "./components/About";
import Work from "./components/Work";
import Hobbies from "./components/Hobbies";
import Skills from "./components/Skills";

function App() {
	const [active, setActive] = useState("about");
	return (
		<div className="root">
			<Menu pointing secondary fluid widths={5} >
				<Menu.Item
					name="DS"
					disabled={true}
					active={false}
				/>
				<Menu.Item
					name="About"
					active={active === "about"}
					onClick={() => setActive("about")}
					icon="info circle"
				/>
				<Menu.Item
					name="Skills"
					active={active === "skills"}
					onClick={() => setActive("skills")}
					icon="terminal"
				/>
				<Menu.Item
					name="Work"
					active={active === "work"}
					onClick={() => setActive("work")}
					icon="briefcase"
				/>
				<Menu.Item
					name="Hobbies"
					active={active === "hobbies"}
					onClick={() => setActive("hobbies")}
					icon="paint brush"
				/>
			</Menu>
			<Container className="App" >
				<About active={active} />
				<Work active={active} />
				<Skills active={active} />
				<Hobbies active={active} />
			</Container>
			<Menu attached="bottom" text className="footer">
				<Menu.Item header>Made by Dhruvil Shah with <Icon name="heart" style={{ paddingLeft: 3 }} /> and <Icon name="coffee" style={{ paddingLeft: 3 }} /></Menu.Item>
			</Menu>
		</div>
	);
}

export default App;